import { NextResponse } from "next/server"

export async function GET() {
    const token = process.env.NEXT_PUBLIC_BASEROW_API_TOKEN
    const tableBooks = process.env.NEXT_PUBLIC_BASEROW_TABLE_BOOKS

    return NextResponse.json({
        tokenDefined: !!token,
        tokenLength: token?.length || 0,
        tableBooksDefined: !!tableBooks,
        envKeys: Object.keys(process.env).filter(k => k.startsWith("NEXT_PUBLIC_BASEROW"))
    })
}
